                    return;
                }
                axios.get("https://www.easy-mock.com/mock/5d2d749489a4187429346960/login/property", {
                    params: {
                        keywords: this.search,
                    },
                }).then(res => {
                    if (res.data.data.code === 200) {
                        this.tableData = res.data.data.data;
                        this.total = res.data.data.total;
                    } else {
                        this.$message.error("")
                    }
                })
            },
            arraySpanMethod({row, column, rowIndex, columnIndex}) {

            },
            selectionChange(selection) {
                this.arr = selection.map(v => v.id);
                console.log(this.arr.toString());
            },
            del() {
                if (this.arr.length === 0) {
                    this.$message.error("");
                    return
                }
                axios.delete("https://www.easy-mock.com/mock/5d2d749489a4187429346960/login/property", {
                    params: {
                        ids: this.arr.toString(),
                    },
                }).then(res => {
                    if (res.data.data.code === 200) {
                        this.$message.success("");
                        this.getfecth();
                    } else {
                        this.$message.error("")
                    }
                })
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error(' JPG !');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isJPG && isLt2M;
            },
            submit() {
                // this.dialogFormVisible = true;
                axios.post("https://www.easy-mock.com/mock/5d2d749489a4187429346960/login/property").then(res => {
                    if (res.data.data.code === 200) {
                        this.$message.success("");
                        this.dialogFormVisible = false;
                        this.getfecth();
                    } else {
                        this.$message.error("")
                    }
                })
            },
            view(id) {
                this.dialogFormVisible = true;
                axios.get("https://www.easy-mock.com/mock/5d2d749489a4187429346960/login/property", {
                    params: {
                        id
                    }
                }).then(res => {
                    console.log(res);
                    if (res.data.data.code === 200) {
                        this.form1 = res.data.data.data;
                        console.log(this.form1);
                    } else {
                        this.$message.error("")
                    }
                })
            },
            edit() {
                this.dialogFormVisible = true;
                axios.put("https://www.easy-mock.com/mock/5d2d749489a4187429346960/login/property", {
                    params: {
                        id
                    }
                }).then(res => {
                    if (res.data.data.code === 200) {

                    } else {
                        this.$message.error("")
                    }
                })
            },
        }
        ,
        mounted() {
            this.getfecth()
        }
    }
</script>

